	module.exports = {"data":{"markdownRemark":{"html":"<p>引っ越したのはもう2週間ほど前だが、今日やっと前のアパートの契約期限が終わって解約の立ち会いに嵐山まで行ってきましたよ。 もう周囲の風景が懐かしく見える。そして空気がうまい。いざ家具が無くなると今まで狭い狭いと思ってた部屋が無茶苦茶広く感じる。きっとレイラのせいに違いない。 管理会社「…ここの穴、補修代もらうよ」 ああっお願いだから許してドラえ悶 帰路はそのまま下ってディーゼル→秩父路急行→MAXたにがわ新幹線ってVIPしまくりんぐルートで考えてたが、どこをどう乗り違えたか三峰口行きの秩父路急行乗っちゃった。このまま引き返すのも悔しいからレッドアロー特急で帰ったさ 'Ａ｀</p>","frontmatter":{"cover":null,"date":"2007-07-12T20:48:00+09:00","strDate":"2007/7/12","title":"嵐山のアパートやっと解約完了"}}},"pathContext":{"path":"/ja/blog/2007/07/12-嵐山のアパートやっと解約完了/","langKey":"ja"}}

